import React from 'react'
import './privacy.scss';

export default function Privacypage() {
    return (
        <>
            <div className="privacypage">
                <div className="conatiner-privacy">
                    <h1>Privacy Policy</h1>
                </div>
            </div>

            <div className="conatiner-privacy">
                <div className='main-sub-name'>
                    <h2>Privacy Policy Of Forex Voyager Limited</h2>
                </div>
                <p>It is the stated policy of Forex Voyager Limited (hereafter referred to as Forex, Forex Voyager, us, we and our) that Forex Voyager will act in accordance with current best practice with regard to the processing of personal data.</p>

                <p>Your privacy is very important to us and we make it our priority to safeguard and secure any and all confidential information relating to individuals.</p>

                <p>Forex Voyager understands the importance of maintaining the privacy of your personal information. Forex Voyager as a company would like to assure you of our commitment to keeping it private</p>


                <div className='main-sub-name'>
                    <h2>PRIVACY PROTECTION</h2>
                </div>
                <p>Forex Voyager has appointed a Data Protection Officer to ensure that our Company manages/processes your personal information in compliance with the applicable data protection laws and regulations and in accordance with this Privacy Policy.</p>

                <p>In relation to your personal data, you agree that we use and rely on the data you provided us, and we and our group companies can store and process them both in physically and/or electronically</p>

                <p>It is your responsibility to make sure that your password is only known to you and not disclosed to anyone else. Your registered information is securely stored with us, and only authorized staff have access to this information. Please also note that transfer of information via the internet is not always entirely secure, but we have taken significant precautions to assure that your personal data is being protected.</p>

                <div className='main-sub-name'>
                    <h2>PERSONAL INFORMATION WE COLLECT</h2>
                </div>
                <p>If you decide to apply to open an account with Forex Voyager Limited, there is certain information that we will require from you in order to do so. Forex Voyager obtains most of the information directly from you through application or other forms, and from maintaining records of information provided in the course of ongoing client support. Forex Voyager may also collect information about your use of the website, such as pages visited, frequency and duration and visit and trading activities.</p>

                <p>In relation to your personal data, you agree that we use and rely on the data you provided us, and we and our group companies can store and process them both in physically and/or electronically</p>

                <p>The information that we may collect from you includes the following:</p>

                <div className="personal">
                    <ul>
                        <li>Full Name</li>
                        <li>Residential Address</li>
                        <li>Contact details (e.g. telephone number, email address, etc.)</li>
                        <li>Date of birth, place of birth, gender, citizenship</li>
                        <li>Profession and employment details</li>
                        <li>Authentication data (e.g. signature)</li>
                        <li>Location data</li>
                        <li>Verification information, which includes information necessary to verify your identity such as a passport or drivers license</li>
                    </ul>
                </div>

                <p>Forex Voyager will use information collected from you to provide the information and services requested by you and to improve the quality of our service. Unless you are informed otherwise, the personal information Forex Voyager holds is used for establishing and managing your account, reviewing your ongoing needs, enhancing client support and products, by giving ongoing information or opportunities that may be relevant to you.</p>


                <div className='main-sub-name'>
                    <h2>PROCESSING OF YOUR PERSONAL INFORMATION</h2>
                </div>
                <p>We may process your personal data on the following bases and for the following purposes:</p>

                <div className='main-sub-name'>
                    <h2>1. PERORMANCE OF CONTRACT</h2>
                </div>
                <p>We process personal data in order to provide our services and products, as well as information regarding our products and services based on the contractual relationship with our clients (i.e. so as to perform our contractual obligations). In addition, processing of personal data takes place to be able to complete our client on-boarding/acceptance procedures. In view of the above, we need to verify your identity in order to accept you as our client and we will need to use those details in order to effectively manage your trading account with us. This may include third parties carrying out credit or identity checks on our behalf. The use of your personal information is necessary for us to know who you are as we have a legal obligation to comply with Know Your Customer and customer due diligence regulatory obligations.</p>


                <div className='main-sub-name'>
                    <h2>2. COMPLIANCE WITH A LEGAL OBLIGATION</h2>
                </div>
                <p>In view of the above, we need to verify your identity in order to accept you as our client and we will need to use those details in order to effectively manage your trading account with us. This may include third parties carrying out credit or identity checks on our behalf. The use of your personal information is necessary for us to know who you are as we have a legal obligation to comply with Know Your Customer and customer due diligence regulatory obligations</p>

                <div className='main-sub-name'>
                    <h2>3. FOR THE PURPOSES OF SAFEGUARDING LEGITIMATE INTERESTS</h2>
                </div>
                <p>We process personal data so as to safeguard the legitimate interests pursued by us or by a third party. A legitimate interest is when we have a business or commercial reason to use your information. Examples of such processing activities include the following:</p>

                <ul className='ul-lines'>
                    <li>nitiating legal claim and preparing our defense in litigation procedures</li>
                    <li>Risk management</li>
                    <li>Measures to manage business and for further developing and services</li>
                    <li>Measures to ensure IT and system security, preventing potential crime, asset security, admittance controls and anti-trespassing measures</li>
                    <li>Sharing your personal data within Forex Voyager for the purpose of updating/verifying your personal data in accordance with the relevant anti-money laundering compliance framework.</li>
                </ul>

                <div className='main-sub-name'>
                    <h2>4. YOU HAVE PROVIDED YOUR CONSENT</h2>
                </div>
                <p>Our storage and use of your personal data is based on your consent (other than for the reasons described or implied in this policy when your consent is not required). You may revoke consent at any time; however, any processing of personal data prior to the receipt of your revocation will not be affected.</p>

                <div className='main-sub-name'>
                    <h2>5. TO PROVIDE YOU WITH PRODUCTS AND SERVICES, INFORMATION</h2>
                </div>
                <p>Once you successfully open a trading account with us, or subscribe to an update, we will need to use your personal information to perform our services and comply with our obligations to you. It is also in our legitimate interests to try to ensure that we are providing the best products and services so we may periodically review your needs based on our assessment of your personal information to try to ensure that you are getting the benefit of the best possible products and services from us.</p>

                <div className='main-sub-name'>
                    <h2>6. TO INVESTIGATE OR SETTLE ENQUIRIES OR DISPUTES</h2>
                </div>
                <p>We may need to use personal information collected from you to investigate issues or to settle disputes with you because it is in our legitimate interests to ensure that issues and disputes get investigated and resolved in a timely and efficient manner.</p>

                <div className='main-sub-name'>
                    <h2>7. TO COMPLY WITH APPLICABLE LAWS, COURT ORDERS, OTHER JUDICIAL PROCESS, OR THE REQUIREMENTS OF ANY APPLICABLE REGULATORY AUTHORITIES</h2>
                </div>
                <p>We may need to use your personal information to comply with any applicable laws and regulations, court orders or other judicial process, or the requirements of any applicable regulatory authority. We do this not only to comply with our legal obligations but because it may also be in our legitimate interest to do so.</p>

                <div className='main-sub-name'>
                    <h2>8. DATA ANALYSIS</h2>
                </div>
                <p>Our web pages and emails may contain web beacons or pixel tags or any other similar type of data analysis tools that allow us to track receipt of correspondence and count the number of users that have visited our webpage or opened our correspondence. We may aggregate your personal information (such as trading history) with the personal information of our other clients on an anonymous basis. If your personal information is not in an anonymized form, it is in our legitimate interest to continually evaluate that personal information to ensure that the products and services we provide are relevant to the market.</p>

                <div className='main-sub-name'>
                    <h2>9. MARKETING PURPOSES</h2>
                </div>
                <p>We may use your personal information to send you marketing communications by email, phone, SMS or other methods (such as social media channels) to ensure that you are kept up to date with our latest products and services. We will not disclose your information to any outside parties that may allow them to directly market to you.</p>

                <div className='main-sub-name'>
                    <h2>10. INTERNAL BUSINESS PURPOSES AND RECORD KEEPING</h2>
                </div>
                <p>We may use your personal information for internal business and research or record keeping purposes. Such processing is in our own legitimate interests and is required in order to comply with our legal obligations. This may include any communications that we have with you in relation to the services and products we provide to you. We will also keep records to ensure that you comply with your contractual obligations pursuant to the agreement governing our relationship with you.</p>

                <div className='main-sub-name'>
                    <h2>11. LEGAL NOTIFICATIONS</h2>
                </div>
                <p>Often the law requires us to advise you of certain changes to products or services or laws. We may need to inform you of changes to the terms or the features of our products or services. We need to process your personal information to send you these legal notifications. You will continue to receive this information from us even if you choose not to receive direct marketing information from us.</p>

                <div className='main-sub-name'>
                    <h2>12. PHYSICAL SECURITY</h2>
                </div>
                <p>If you enter any of our premises we may record your image on our CCTV for security reasons. We may also take your details to keep a record of who has entered our premises on any given day. It is in our legitimate interest to do this to maintain a safe and secure working environment.</p>

                <div className='main-sub-name'>
                    <h2>DISCLOSURE OF YOUR PERSONAL INFORMATION</h2>
                </div>
                <p>The Company will not disclose any of its clients confidential information to a third party, except:</p>

                <ul className='disclosure'>
                    <li>to the extent that it is required to do so pursuant to any applicable laws, rules or regulations;</li>
                    <li>if there is a duty to disclose;</li>
                    <li>if our legitimate business interests require disclosure; or</li>
                    <li>at your request or with your consent or to Persons described in this policy. The Company will endeavor to make such disclosures on a needto-know basis, unless otherwise instructed by a regulatory authority. Under such circumstances, the Company will notify the third party regarding the confidential nature of any such information.</li>
                </ul>

                <p>As part of using your personal data for the purposes set out above, the Company may disclose your personal information to the following:</p>

                <ul className='ul-lines'>
                    <li>Any companies under Forex Voyager, meaning any of our ultimate holding companies and their respective subsidiaries may receive such information;</li>
                    <li>Our associates and service providers, for business purposes, including third parties such as business service providers and specialist advisers who have been contracted to provide us with administrative, financial, legal, tax, compliance, insurance, research or other services;</li>
                    <li>Business introducers with whom we have a mutual business relationship;</li>
                    <li>Business parties, credit providers, courts, tribunals and regulatory authorities as agreed or authorized by law;</li>
                    <li>Anyone authorized by you.</li>
                </ul>

                <p>If the Company discloses your personal information to business parties, such as card or other payment processing companies or banks, in order to perform the services requested by clients, such third parties may store your information in order to comply with their legal and other obligations.</p>

                <p>Clients accept and consent that the Company may, from time to time, analyze the data collected while visiting our website(s) or by other means, such as questionnaires, for statistical purposes in order to improve the Companys business activities.</p>

                <div className='main-sub-name'>
                    <h2>TRANSFERS OUTSIDE OF THE EUROPEAN ECONOMIC AREA</h2>
                </div>
                <p>We may transfer your personal information outside the European Economic Area (EEA) to other Forex Voyager companies as well as service providers (i.e. processors) who are engaged on our behalf. To the extent we transfer your information outside the EEA, we will ensure that the transfer is lawful and that processors in third countries are obliged to comply with the European data protection laws or other countries laws which are comparable and to provide appropriate safeguards in relation to the transfer of your data in accordance with GDPR Article 46.</p>
                <p>By submitting your personal data, you consent to your personal information being processed by staff of Forex Voyager operating outside the EEA who work for us. Such staff may be, among others, engaged in the fulfilment of your requests, the processing of your payment details and the provision of support services. The Company will take all steps reasonably necessary to ensure that your data is treated securely and in accordance with this Privacy Policy</p>

                <div className='main-sub-name'>
                    <h2>YOUR COLLECTED INFORMATION</h2>
                </div>
                <p>Tracking systems used on the Companys website(s) may collect your personal data in order to optimize the services provided to clients/potential clients. The website collects information in the following ways:</p>

                <div className='main-sub-name'>
                    <h2>DEVICE INFORMATION</h2>
                </div>
                <p>By recognizing your device used to access and use the Companys website(s), we can provide you with the most appropriate version of our website(s).</p>

                <div className='main-sub-name'>
                    <h2>LOG INFORMATION</h2>
                </div>
                <p>Logging certain behaviors on the site enables the company to track user action and therefore troubleshoot any issues that may occur.</p>

                <div className='main-sub-name'>
                    <h2>LOCATION INFORMATION</h2>
                </div>
                <p>Using your IP address helps us localize our website content, which we provide to you based on your country, and improve your user experience on our site(s). For further details about our cookie policy and how our cookies work, read our Privacy Policy.</p>

                <div className='main-sub-name'>
                    <h2>HOW WE OBTAIN YOUR CONSENT</h2>
                </div>
                <p>If our use of your personal information requires your consent, such consent will be provided in accordance with the express written terms which govern our business relationship (which are available on our website(s), as amended from time time), or any other contract we may have entered into with you or as set out in our communication with you from time to time</p>

                <p>If we rely on your consent as our legal basis for holding and processing your personal information, you have the right to withdraw that consent at any time by contacting us using the contact details set out in this Privacy Policy.</p>


                <div className='main-sub-name'>
                    <h2>STORAGE OF YOUR PERSONAL INFORMATION AND RETENTION PERIOD</h2>
                </div>
                <p>In relation to your personal information, we will hold this for as long as we have a business relationship with you, in a combination of secure computer storage facilities and paper-based files and other records and we take the necessary measures to protect the personal information we hold from misuse, loss, unauthorized access, modification or disclosure.</p>

                <p>When we consider that personal information is no longer necessary for the purpose for which it was collected, we will remove any details that will identify you or we will securely destroy the records. However, we may need to maintain records for a significant period of time (after you cease being our client). For example, we are subject to certain antimoney laundering laws which require us to retain the following, for a period of 5 years after our business relationship with you has ended:</p>

                <p>A copy of the documents we used in order to comply with our customer due diligence obligations;</p>

                <p>Supporting evidence and records of transactions with you and your relationship with us Also, the personal information we hold in the form of a recorded communication, by telephone, electronically, in person or otherwise, will be held in line with local regulatory requirements (i.e., 5 years after our business relationship with you has ended or longer if you have legitimate interests (such as handling a dispute with you). If you have opted out of receiving marketing communications we will hold your details on our suppression list so that we know you do not want to receive these communications.</p>

                <div className='main-sub-name'>
                    <h2>YOUR RIGHTS REGARDING YOUR PERSONAL INFORMATION INFORMATION AND ACCESS</h2>
                </div>
                <p>You have the right to request what information we process, along with a copy of that personal information within thirty (30) days from the date of your request. If you require additional copies, we may need to charge an administration fee.</p>

                <div className='main-sub-name'>
                    <h2>RECTIFICATION</h2>
                </div>
                <p>We will take all reasonable steps to make sure that your personal information remains accurate, complete and up-to-date. If the personal information we hold about you is inaccurate or incomplete, you are entitled to have it rectified. If we have disclosed your personal information to others, we will let them know about the rectification where possible. You may inform us at any time that your personal details have changed by emailing us at support@ForexVoyager.com The Company will change your personal information in accordance with your instructions. To proceed with such requests, in some cases we may need supporting documents from you as proof, i.e. personal information that we are required to keep for regulatory or other legal purposes.</p>

                <div className='main-sub-name'>
                    <h2>ERASURE</h2>
                </div>
                <p>You can ask us to delete or remove your personal information in certain circumstances such as if we no longer need it or you withdraw your consent (if applicable) provided that we have no legal obligation to retain that data. Such requests will be subject to any retention limits we are required to comply with in accordance with applicable laws and regulations. If we have disclosed your personal information to others, we will let them know about the erasure where possible.</p>

                <div className='main-sub-name'>
                    <h2>PROCESSING RESTRICTIONS</h2>
                </div>
                <p>You can ask us to block or suppress the processing of your personal data in certain circumstances such as if you contest the accuracy of that personal information or object to us processing it. It will not stop us from storing your personal information. We will inform you before we decide not to agree with any requested restriction. If we have disclosed your personal information to others, we will inform you about the restriction if possible.</p>

                <div className='main-sub-name'>
                    <h2>DATA PORTABILITY</h2>
                </div>
                <p>Under the General Data Protection Regulation (679/2016), you have the right, in certain circumstances, to obtain personal information you have provided us with (in a structured, commonly used and machine readable format) and to re-use it elsewhere or ask us to transfer this to a third party of your choice.</p>

                <div className='main-sub-name'>
                    <h2>OBJECTION</h2>
                </div>
                <p>You can ask us to stop processing your personal information, and we will do so, if we are:</p>

                <p>Relying on our own or someone elses legitimate interests to process your personal information except if we can demonstrate compelling legal grounds for the processing;</p>

                <p>Processing your personal information for direct marketing;</p>

                <p>Processing your personal information for research unless we reasonably believe such processing is necessary or prudent for the performance of a task carried out in the public interest (such as by a regulatory or enforcement agency).</p>

                <div className='main-sub-name'>
                    <h2>OPT-OUT OF SUBMITTING YOUR PERSONAL INFORMATION</h2>
                </div>
                <p>If you do not want us to use your personal information, you must inform the Company by sending an email to support@ForexVoyager.com . If you decide to do so, we may not be able to continue to provide information, services and/or products requested by you and we will have no liability to you in this respect.</p>

                <div className='main-sub-name'>
                    <h2>LEGAL DISCLAIMER</h2>
                </div>
                <p>The Company may disclose your personally identifiable information as required by rules and regulations and if the Company believes that disclosure is necessary to protect our rights or to comply with other proceedings, court order, legal process served or pursuant to governmental, intergovernmental or other regulatory bodies. The Company is not liable for misuse or loss of personal information or otherwise on the Companys website(s) that the Company does not have access to or control over. The Company will not be liable for unlawful or unauthorized use of your personal information due to misuse or misplacement of your passwords, negligent or malicious intervention or otherwise by you or due to your acts or omissions or a person authorized by you (whether or to that authorization is permitted by the terms of our legal relationship with you).</p>

                <div className='main-sub-name'>
                    <h2>Forex Voyager Card Acceptance</h2>
                </div>
                <p>All credit/debit cards details and personally identifiable information will NOT be stored, sold, shared, rented or leased to any third parties</p>

                <div className='main-sub-name'>
                    <h2>HOW TO CONTACT US</h2>
                </div>
                <p>If you have any questions or concerns regarding this Privacy Policy, please email us at <a href="mailto:Info@forexVoyager.com" target='_blank' rel="noreferrer">Info@forexVoyager.com</a></p>
            </div>
        </>
    )
}
